<template>
  <div>
    <p class="title" v-html="getTitle()">
    </p>
    <a-table 
      :bordered="false" 
      :dataSource="data" 
      :pagination="{hideOnSinglePage: true}"
      size="small"
      rowKey="attrId"
    > 
      <a-table-column 
        :title="this.$t('task.index1')"
        align="center"
        width="100px"
      >
        <span slot-scope="text">
          <a-icon type="exclamation-circle" theme="twoTone" class="icon" style="font-size:25px" twoToneColor="red" />
        </span>
      </a-table-column>
      <a-table-column
        :title="this.$t('device.index1')"
        dataIndex="deviceNum"
        width="200px"
      >
        <span slot-scope="text" class="error">
          {{ text }}
        </span>
      </a-table-column>
      <a-table-column
        :title="this.$t('device.index2')"
        dataIndex="description"
        width="200px"
      >
        <span slot-scope="text" class="error">
          {{ text }}
        </span>
      </a-table-column>
      <!-- 错误码 -->
      <a-table-column
        :title="this.$t('common.errorCode')"
        dataIndex="errorCode"
        width="200px"
      >
        <span slot-scope="text" class="error">
          {{ text }}
        </span>
      </a-table-column>
      <a-table-column
        :title="this.$t('common.fail_reason')"
        dataIndex="result"
      >
        <span slot-scope="text,record" class="error">
          {{ $t('err.'+record.errorCode)!=('err.'+record.errorCode)?$t('err.'+record.errorCode):text }}
        </span>
      </a-table-column>
        
    </a-table>
  </div>
</template>

<script>
import STable from '@/components/table/'
  export default {
     components: {
       STable
    },
    props: {
      importResult: {
        type: Object ,
        default: null
      }
    },
    data() {
      return {
        total: this.importResult.result.total,
        success: this.importResult.result.success,
        fail: this.importResult.result.fail,
        data:{}
      }
    },
    mounted(){
      var failList=this.importResult.result.failList
      this.data=failList
    },
    methods: {
      getTitle() {
        const total = `<span style="color: #1890ff; font-weight: 500">${this.total}</span>`
        const success = `<span style="color: #00660F; font-weight: 500">${this.success}</span>`
        const fail = `<span style="color: red; font-weight: 500">${this.fail}</span>`
        return this.$t('common.import_info1',{total,success,fail})
      },
      getReason(text){
        if(text == -103159) {
          return this.$t('line.import_error_tips1')
        }else if(text == -103016) {
          return this.$t('line.import_error_tips2')
        }else if(text == -103035) {
          return this.$t('line.import_error_tips3')
        }else if(text == -103035) {
          return this.$t('line.import_error_tips4')
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
.error {
  color: red;
}
.normal{
  color:red;
}
.success{
  color:#00660F;
}
.title {
  font-size: 20px;
}
</style>