<template>
  <div style="height: 100%">
    <div :spinning="loading" :tip="$t('device.tip')" style="height: 100%">
      <a-card class="card" :bordered="false" style="height: 100%">
        <a-form :form="form" class="form">
          <a-row class="form-row" :gutter="16">
            <a-col :lg="7" :md="7" :sm="24">
              <a-form-item :label="this.$t('meter.index3')">
                <a-input-group compact>
                  <a-input 
                    style="width:80%" 
                    :disabled="true" 
                    v-decorator="[
                      'tmName',
                      {initialValue: data.description, rules: [{required: true,message:$t('meter.index3')+' '+$t('common.notnull')}] }
                    ]" />
                  <span style="width:20%">
                    <a-button @click="queryTm" icon="search" style="width:100%"></a-button>
                  </span>
                </a-input-group>
              </a-form-item>
            </a-col>
            <a-col :xl="{span: 7, offset: 1}" :lg="{span: 7, offset: 1}" :md="{span: 24}" :sm="24">
              <a-form-item
              >
                <a-upload accept=".xlsx" :showUploadList="false" :file-list="fileList" :remove="handleRemove" :before-upload="beforeUpload">
                  <a-button type="primary" icon="upload" ghost > {{ $t('button._007') }} </a-button>&nbsp;&nbsp;
                </a-upload>
                <a download="distribution.xlsx" href="/inhe-platform-mdm/loading/distribution.xlsx" class="download">{{ $t('common.download_model') }}</a>
                <br>
                <a-input v-model="xlsxFile" :readOnly="true" :allowClear="true" :placeholder="$t('common.import_xlsx')" >
                  <a-icon slot="suffix" type="delete" @click="handleRemove" v-if="fileList.length > 0"/>
                </a-input>
              </a-form-item>
            </a-col>
          </a-row>
        </a-form>
      </a-card>
    </div>
    <search-transformer ref="selectTm" @ok="loadTmData" :params="serachParam"></search-transformer>
  </div>
</template>
<script>
  import Dict from '@/views/public/Dict'
  import SearchTransformer from '@/views/public/SearchTransformer'
  export default {
    components: {
      Dict,
      SearchTransformer
    },
    data() {
      return {
        loading: false,
        isPost:false,
        url: '',
        xlsxFile:'',
        form: this.$form.createForm(this),
        data: {},
        serachParam:{deptId:''},
        param: {},
        fileList: [],
        stationId:'',
        submainData: {},
        formItemLayout: {labelCol: {lg: {span: 7}, sm: {span: 7}},wrapperCol: {lg: {span: 10}, sm: {span: 17}}},
      }
    },
    props: {
      params: {
        type: Object,
        default: null
      }
    },

    methods: {
      queryTm() {
        this.$refs.selectTm.showModal()
      },
      loadTmData(item) {
        this.data.tmId = item[0]?item[0].id:''
        this.data.lineId = item[0]?item[0].lineId:''
        this.data.deptId = item[0]?item[0].deptId:''
        this.form.setFieldsValue({'tmName': item[0].description})
      },
      handleRemove(file) {
        console.log(file)
        this.xlsxFile=''
        const index = this.fileList.indexOf(file)
        const newFileList = this.fileList.slice()
        newFileList.splice(index, 1)
        this.fileList = newFileList
      },
      beforeUpload(file) {
        console.log(file)
        this.xlsxFile=file.name
        this.fileList = [...this.fileList, file]
        return false
      },
      onSubmit() {
        var that=this
        let result = false      
        that.form.validateFields((err, values) => {
          console.log(values)
          if (!err) {
            if(!that.fileList.length) {
              that.$message.info(that.$t('common.import_info7'))
              return
            }
            var formData = new FormData()
            formData.append('fileName',  that.fileList[ that.fileList.length-1])
            formData.append('tmId', that.data.tmId)
            formData.append('lineId', that.data.lineId)
            formData.append('deptId', that.data.deptId)
            result=formData
            console.log(result)
          }
        })
        return result
    },
      onCancel(){
        this.$emit('cancel')
      },
    }
  }
</script>
<style lang="scss" scoped>
  .card{
    margin-bottom: 24px;
  }
  .ant-upload-list{
    display: none;
  }
  .download{
    text-decoration: underline;
  }
  .ant-upload-list{
    width:100%;
    border: red;

  }
</style>
