<template>
  <div style="background-color: #fff;padding:10px 20px">
    <div :spinning="loading">
      <a-form :form="form" style="margin-top: 10px">
        <a-row :gutter="16" style="border-bottom:1px solid #ddd;margin-top:20px">
          <span style="display:inline-block;padding:10px 10px;font-size:15px">{{ $t('device.title1') }} :</span>
        </a-row>
        <a-row :gutter="16">
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="$t('meter.index1')">
              <a-input 
                v-decorator="[
                  'description', 
                  {initialValue: data.description, rules: [{required: true,message:$t('meter.index1')+' '+$t('common.notnull')}] }
                ]"
              />
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('meter.index2')">
              <a-select
                v-decorator="[
                  'meterSort',
                  { initialValue: data.meterSort?[data.meterSort]:['3'],rules: [{required: true,message:$t('meter.index2')+' '+$t('common.notnull')}] }
                ]"
              >
                <a-select-option value="3">
                  {{ $t('dct.METER_SORT3') }}
                </a-select-option>
                <a-select-option value="1">
                  {{ $t('dct.METER_SORT1') }}
                </a-select-option>
                <a-select-option value="2">
                  {{ $t('dct.METER_SORT2') }}
                </a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('dept.index')">
              <dept-select
                @change="getId"
                :disabled="params.opType!='1'"
                v-decorator="[
                  'deptId',
                  {initialValue: data.deptId?data.deptId:'',rules: [{required: true,message:$t('dept.index_null')}]}
                ]" />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('meter.index3')">
              <a-input-group compact>
                <a-input 
                  style="width:80%" 
                  :disabled="true" 
                  v-decorator="[
                    'tmName',
                    {initialValue: data.tmDescription, rules: [{required: true,message:$t('meter.index3')+' '+$t('common.notnull')}] }
                  ]" />
                <span style="width:20%">
                  <a-button @click="queryTm" icon="search" style="width:100%"></a-button>
                </span>
              </a-input-group>
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('meter.index4')">
              <a-select
                v-decorator="[
                  'important',
                  { initialValue: data.important?[data.important]:['N'],rules: [{required: true,message:$t('meter.index4')+' '+$t('common.notnull')}] }
                ]"
              >
                <a-select-option value="Y">
                  {{ $t('common.yes') }}
                </a-select-option>
                <a-select-option value="N">
                  {{ $t('common.no') }}
                </a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('common.status')">
              <dict 
                type="DEVICE_STATUS"
                table="params"
                v-decorator="[
                  'status',
                  { initialValue: data.status?[data.status]:['0'],rules: [{required: true,message:$t('common.status')+' '+$t('common.notnull')}] }
                ]"
              ></dict>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('common.vol_level')">
              <dict 
                type="VOL_LEVEL"
                table="params"
                v-decorator="[
                  'volLevel',
                  { initialValue: data.volLevel?[data.volLevel]:['0'],rules: [{required: true,message:$t('common.vol_level')+' '+$t('common.notnull')}] }
                ]"
              ></dict>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16" style="border-bottom:1px solid #ddd;margin-top:50px">
          <span style="display:inline-block;padding:10px 10px;font-size:15px">{{ $t('device.title2') }} :</span>
        </a-row>
        <a-row :gutter="16">
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('device.index1')">
              <a-input-group compact>
                <a-input 
                  style="width:80%" 
                  :disabled="true" 
                  v-decorator="[
                    'deviceNum',
                    {initialValue: data.deviceNum, rules: [{required: true,message:$t('device.index1')+' '+$t('common.notnull')}] }
                  ]" />
                <span style="width:20%">
                  <a-button @click="queryDev" icon="search" style="width:100%" :disabled="params.opType!='1'"></a-button>
                </span>
              </a-input-group>
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="$t('device.index3')">
              <a-input 
                :disabled="true"
                v-decorator="[
                  'address', 
                  {initialValue: data.address}
                ]"
              />
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="$t('meter.index5')">
              <pick-date
                showTime
                :defaultValue="data?data.regDate:''"
                v-decorator="[
                  'regDate'
                ]"
              ></pick-date>
            </a-form-item>
          </a-col>
        </a-row>
      </a-form>
    </div> 
    <search-transformer ref="selectTm" @ok="loadTmData" :params="serachParam"></search-transformer>
    <search-device ref="selectDev" @ok="loadDeviceData" :params="serachParam"></search-device>
    <footer-tool-bar :style="{ width: '100%'}">
      <a-button @click="onCancel">{{ $t('common.btn_cancel') }}</a-button>&nbsp;&nbsp;
      <a-button @click="onSubmit" type="primary" v-if="params.opType!='3'">{{ $t('common.btn_ok') }}</a-button>
    </footer-tool-bar>
  </div>
</template>
<script>
  import { axios } from '@/utils/request'
  import Dict from '@/views/public/Dict'
  import moment from 'moment'
  import DeptSelect from '@/views/public/DeptSelect'
  import SearchTransformer from '@/views/public/SearchTransformer'
  import SearchDevice from '@/views/public/SearchDevice'
  import FooterToolBar from '@/components/FooterToolbar'
  import notification from 'ant-design-vue/es/notification'

  export default {
    components: {
      Dict,
      FooterToolBar,
      notification,
      DeptSelect,
      SearchTransformer,
      SearchDevice,
    },
    data() {
      return {
        loading: false,
        form: this.$form.createForm(this),
        data: {status:0,vol_level:0},
        serachParam:{deptId:''},
        confirmLoading: false,
        formItemLayout: {labelCol: {lg: {span: 7}, sm: {span: 7}},wrapperCol: {lg: {span: 14}, sm: {span: 14}}}
      }
    },
    props: {
      params: {
        type: Object,
        default: null
      }
    },
    mounted(){
      this.initModel()
    },
    methods: {
      moment,
      getId(values){
         if(this.params.opType=='1'){
            this.data.tmId=''
            this.data.id=''
            this.data.lineId=''
            this.form.setFieldsValue({'tmName': ''})
            this.form.setFieldsValue({'deviceNum': ''})
            this.form.setFieldsValue({'address': ''})
         }
         this.serachParam.deptId=values
      },
      queryTm() {
        this.$refs.selectTm.showModal()
      },
      loadTmData(item) {
        this.data.tmId = item[0]?item[0].id:''
        this.data.lineId = item[0]?item[0].lineId:''
        this.form.setFieldsValue({'tmName': item[0].description})
      },
      queryDev() {
        this.$refs.selectDev.showModal()
      },
      loadDeviceData(item) {
        this.data.id=item[0].devId
        this.form.setFieldsValue({'deviceNum': item[0].devNo})
        this.form.setFieldsValue({'address': item[0].devNum})
      },
      initModel(){
        if(this.params.opType=='2' || this.params.opType=='3'){
          this.initData()
        }else{
          this.data={status:0,vol_level:0}
        }
      },
      initData(){
        const that = this
        that.loading = true
        axios({
          url:'/archives/distribution/update-load',
          method: 'get',
          params: {id: that.params.id}
        }).then(response=>{
          that.loading = false
          this.serachParam.deptId=response.result.deptId
          that.data = response.result
        })
      },
      onSubmit(){
        this.form.validateFields((err,values) => {
          if (!err) {            
            const that = this
            that.data = Object.assign(that.data,values)
            if(!that.params){
              return
            }
            that.data.status = that.data.status[0]
            that.data.volLevel = that.data.volLevel[0]
            that.data.meterSort = that.data.meterSort[0]
            that.data.important = that.data.important[0]
            that.loading = true
            axios({
              url: that.params.opType=='1'?'/archives/distribution/insert':'/archives/distribution/update',
              method: 'post',
              params: that.data
            }).then(result=>{
              that.loading = false
              if(result){
                this.$emit('ok',result)
              }
            })
          }
        })
      },
      onCancel(){
        this.$emit('cancel')
      },
    }
  }
</script>
<style lang="scss" scoped>
  .card{
    margin-bottom: 24px;
  }
</style>
