<template>
  <a-spin :spinning="loading" :tip="$t('task.tip')" size="large">
    <div :bordered="false" v-if="!isEdit && (!isImport)">
      <a-card>
        <div class="table-page-search-wrapper" style="margin-bottom: 10px;">
          <a-row>
            <a-col :md="21" :sm="21">
              <a-button-group>
                <btn-add type="bt" :text="$t('device.add_btn')" @click="onAdd"></btn-add>
                <a-dropdown style="margin-top:2px" placement="bottomRight">
                  <a-menu slot="overlay">
                    <a-menu-item key="1">
                      <btn-add @click="importDev" type="b" :text="$t('substation.add_btn1')"/>
                    </a-menu-item>
                  </a-menu>
                  <a-button type="primary"><a-icon style="font-size: 18px" type="ellipsis" /></a-button>
                </a-dropdown>
              </a-button-group>&nbsp;&nbsp;&nbsp;
              <a-input-search v-model="queryParam.devNumName" :placeholder="$t('device.placeholder')" style="width: 280px;display:inline-block" @search="onSearch" @keyup.enter.native="onSearch"/>&nbsp;&nbsp;&nbsp;
              <dept-select ref="dept" style="display:inline-block" @change="(value)=>{value?queryParam.deptId=value:delete queryParam.deptId; this.onSearch()}"/>
            </a-col>
            <a-col :md="3" :sm="3">
              <sorter @refresh="onSearch" @show="showSearch"></sorter>
            </a-col>
          </a-row>
        </div>
        <s-table  
          ref="table"
          :bordered="false"
          :autoLoad="true"
          :data="loadData"
          size="small"
          rowKey="id"
        >
          <a-table-column 
            :title="this.$t('task.index1')"
            width="50px"
            align="center"
          >
            <span slot-scope="text">
              <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('device.index1')"
            width="150px"
            dataIndex="deviceNum"
          ></a-table-column>
          <a-table-column
            :title="this.$t('device.index2')"
            width="150px"
            dataIndex="description"
          ></a-table-column>
          <a-table-column
            :title="this.$t('device.index3')"
            width="150px"
            dataIndex="address"
          ></a-table-column>
          <a-table-column
            :title="this.$t('device.index4')"
            width="150px"
            dataIndex="meterSort"
            :filters="filters1"
            :filterMultiple="false"
            @FilterDropdownVisibleChange="onChange(1)"
          >
            <span slot-scope="text,record">
              <span >{{ $t('dct.METER_SORT'+record.meterSort) }}</span>
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('dept.index')"
            width="150px"
            dataIndex="deptId"
            :customRender="deptId => this.$refs.dept.getLabel(deptId)">
          </a-table-column>
          <a-table-column
            :title="this.$t('common.status')"
            width="150px"
            dataIndex="status"
            :filters="filters"
            :filterMultiple="false"
            @FilterDropdownVisibleChange="onChange(2)"
          >
            <span slot-scope="text,record">
              <btn-switch 
                :defaultChecked="record.status=='0'"
                :checkedChildren="$refs.deviceStatus.getLabel('0')"
                :unCheckedChildren="record.status=='2'?$refs.deviceStatus.getLabel('2'):$refs.deviceStatus.getLabel('1')"
                @change="checked => onSwitch(checked, record)"
              ></btn-switch> 
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('task.action')"
            dataIndex="action"
          > 
            <span slot-scope="text,record">
              <btn-edit @click="onUpdate(record)" type="a"></btn-edit>
              <a-divider type="vertical"/>
              <btn-remove @click="onRemove(record)" type="a" v-if="record.status!=2"></btn-remove>
            </span>
          </a-table-column>
        </s-table>
        <dict type="DEVICE_STATUS" table="params" ref="deviceStatus" v-show="false" />
      </a-card>
      
    </div> 
    <device-edit 
      v-if="isEdit"
      @ok="onSubmit" 
      @cancel="onCancel" 
      :params="editParams"
    ></device-edit>
    <dev-import v-if="isImport" @ok="onCancelImp" @cancel="onCancelImp"></dev-import>
  </a-spin>
</template>
<script>
  import STable from '@/components/table/'
  import DevImport from '@/views/archives/distribution/DevImport'
  import DeviceEdit from '@/views/archives/distribution/DeviceEdit'
  import Dict from '@/views/public/Dict'
  import { axios } from '@/utils/request'
  import DeptSelect from '@/views/public/DeptSelect'
  export default {
    name: 'Index',
    components: {
      STable,
      DeviceEdit,
      Dict,
      DeptSelect,
      DevImport
    },
    data () {
      return { 
        loading: false,
        reset_flag: true,
        isEdit: false,
        isImport: false,
        show: true,
        editParams: {},       
        queryParam: {}, 
        row:{},  
        laloParams:{la:0,lo:0},
        visible:false,
        filters: [{text: 'none',value: '0'}],
        filters1: [{text: this.$t('dct.METER_SORT3'),value: '3'},{text: this.$t('dct.METER_SORT1'),value: '1'},{text: this.$t('dct.METER_SORT2'),value: '2'}],
        loadData: parameter => {     
              const that = this
              parameter.status instanceof Array?that.queryParam.status = parameter.status[0]:'0' 
              parameter.meterSort instanceof Array?that.queryParam.meterSort = parameter.meterSort[0]:''        
             return axios({
              url: '/archives/distribution/search-list-dis',
              method: 'get',
              params: Object.assign(parameter, this.queryParam)
            }).then(res => { 
              return res.result
            })
        }
      }
    },
    methods: {
      onReset(){
        const that = this
        that.queryParam = {}
        that.reset_flag = false
        this.$nextTick(() => {
          that.reset_flag = true
        })
      },
      onSearch() {
        this.$refs.table.refresh()
      },
      showSearch() {
        this.show = !this.show
      },
      onAdd() {
        this.editParams = {opType: '1'}
        this.isEdit = true
      },
      onUpdate (record) {       
        this.editParams = {opType: '2',id: record.id}
        this.isEdit = true  
      },
      importDev(){
        const that = this
        that.isImport = true
      },  
      onCancelImp() {
        this.isImport = false
      },
      onChange(type){
        if(type==1){
         // this.filters1 = this.filters1.getAll()
        }else{
          this.filters = this.$refs.deviceStatus.getAll()
        }
       
        
      },
      onSwitch(checked, record){
        const that = this
        axios({
          url: '/archives/distribution/switch',
          method: 'post',
          params: {id: record.id, status: checked?'0':'1'}
        }).then(()=>{
          that.$refs.table.refresh()
        })
      },
      onRemove (record) {
        var _this = this
        axios({
          method: 'delete',
          url: '/archives/distribution/delete',
          params: {id: record.id}
        }).then(function () {            
          _this.$refs.table.refresh()
        })
      },
      onSubmit(){
        this.isEdit = false
        this.$refs.table.refresh()
      },
      onCancel(){
        this.isEdit=false
      }
    }
  }
</script>
<style lang="scss">
.error {
  color: red;
}
.normal {
  color: #00660f;
}
.ant-card-head-title{
  padding:12px 0
}
</style>