<template>
  <div style="height: 100%">
    <a-card class="card" :bordered="false" style="height: 100%;margin-bottom:40px;">
      <a-steps class="steps" :current="currentTab">
        <a-step :title="$t('common.step1')" />
        <a-step :title="$t('common.stepok')" />
      </a-steps>
      <div class="content">
        <dev-import-step1 
          v-show="currentTab==0" 
          ref="step1" 
        />
        <dev-import-step2 v-if="currentTab==1" :importResult="ImportResult"/>
      </div>
    </a-card>
    <footer-tool-bar :style="{ width: '100%'}">
      <a-button @click="prevStep" :loading="loading">{{ btnPrev }}</a-button>&nbsp;&nbsp;
      <a-button @click="nextStep" type="primary" :loading="loading" v-if="currentTab!=1">{{ btnNext }}</a-button>
    </footer-tool-bar>
  </div>
</template>
<script>
  import { axios } from '@/utils/request'
  import FooterToolBar from '@/components/FooterToolbar'
  import DevImportStep1 from './DevImportStep1'
  import DevImportStep2 from './DevImportStep2'

  export default {
    components: {
      FooterToolBar,
      DevImportStep1,
      DevImportStep2
    },
    data() {
      return {
        currentTab: 0,
        data1: {},
        data2: {},
        loading: false,
        btnPrev: this.$t('common.btn_cancel'),
        btnNext: this.$t('common.btn_ok'),
        data: {},
        url: ''
      }
    },
    watch: {
      currentTab(value) {
        switch (value) {
          case 0:
            this.btnPrev = this.$t('common.btn_cancel')
            this.btnNext = this.$t('common.btn_ok')
            break
          case 1:
            this.btnPrev = this.$t('common.stepok')
            break
        }
      }
    },
    methods: {
      onSubmit(callback) {
        var that=this
        that.loading = true
        axios({
          url: '/archives/distribution/import',
          method: 'post',
          headers: {
            'Content-Type': 'application/json;charset=UTF-8'
          },
          data: that.data1
        }).then(result=>{
           that.loading = false
           if (result) {
             that.ImportResult=result
             callback()
           }
        })
      },
      nextStep() {
        const that = this
        switch (that.currentTab) {
          case 0:
            that.data1 = that.$refs.step1.onSubmit()
            console.log(that.data1)
            //步骤1提交的值
            if (that.data1) {
               that.onSubmit(function () {
                that.currentTab += 1
              })
            }
            break
          case 1:
            that.currentTab = 0
            break            
        }
      },
      prevStep() {
        if (this.currentTab == 0) {
          this.$emit('cancel')
        } else
        if (this.currentTab == 1) {
          this.$emit('ok')
        }
      },
    }
  }
</script>
<style lang="scss" scoped>
  .steps {
    max-width: 750px;
    margin: 16px auto;
  }
  .content {
    margin-top: 30px;
  }
</style>
