<template>
  <div>
    <a-cascader 
      :options="options" 
      :loadData="loadData" 
      :disabled="disabled" 
      :showSearch="true"
      :allowClear="true"
      :treeDefaultExpandedKeys="true"
      v-model="selectValues"
      @change="onChange"
      :placeholder="$t('dept.tree_placeholder')" 
      changeOnSelect
      style="width: 100%"
    />
  </div>
</template>
<script>
import { axios } from '@/utils/request'
import { mapState } from 'vuex'
const nullArray = []      //防止初始化组件时触发两次selectValues监听
export default {
  data () {
    return {
      options: [],
      selectValues: nullArray,
      thisValue: '',
      queryParam: {}
    }
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    selectValues(value){
      if(value.length>0){
        this.$emit('change',value[value.length-1])
      }
      else{
        this.$emit('change')
      }
    },
    value(v){
      const maxLen = parseInt(this.deptId.length / 3)-1
      const that = this
      that.selectValues = []
      for(let i=maxLen;i<v.length/3;i++){
        that.selectValues.push(v.substr(0,(i+1)*3))
      }
    }
  },
  mounted(){
    this.initSelect()
  },
  computed: {
    ...mapState({
      deptId: state => state.user.deptId,
    })
  },
  methods: {
    onChange(value){
      if(value.length>0){
        this.$emit('change',value[value.length-1])
      }
      else{
        this.$emit('change')
      }
    },
    initSelect(){
      const that = this
      that.options = []
      that.selectValues = nullArray
      axios({
        url: '/sys/dept/public-combo-root',
        method: 'get',
        params: {sync: true}
      })
      .then(data=>{
        that.options = data.result
      })
    },
    loadData(selectedOptions) {
      const that = this
      const targetOption = selectedOptions[selectedOptions.length - 1]
      targetOption.loading = true
      that.queryParam.deptId = targetOption.value
      axios({
        url: '/sys/dept/public-combo-child',
        method: 'get',
        params: that.queryParam
      })
      .then(data=>{
        targetOption.loading = false
        targetOption.children = data.result
        that.options = [...that.options]
      })
    },
    searchNode(options,key){
      if(!options){
        return 
      }
      for(let i=0;i<options.length;i++){
        const row = options[i]
        if(row['value'] == key){
          return row['label']
        }
        else 
        if(key.indexOf(row['value']) == 0){
          return this.searchNode(options[i]['children'],key)
        }
      }
    },
    getLabel(key) {
      // console.log(this.searchNode(this.options,key))
      return this.searchNode(this.options,key)?this.searchNode(this.options,key):''
    }
  },
}
</script>